body {
  font-family: Montserrat, Seymour One, sans-serif;
}

.header {
  background-color: #84a178;
  justify-content: space-between;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.header__logo {
  fill: #f8d24e;
  width: 18.6px;
  height: 37.1px;
  position: relative;
}

.header__logo-box {
  position: relative;
}

.header__logo-span {
  color: #f8d24e;
  font-size: 14.874px;
  position: absolute;
  bottom: 1px;
  right: -11px;
}

.header__menu {
  background-color: #0000;
  border: none;
}

.header__icon {
  width: 32px;
  height: 32px;
  fill: #fff;
}

.products {
  width: 100%;
  background-image: url("Backgroud_mobile 1x.15774d75.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  align-items: center;
  margin: 0 auto;
}

.products__label, .products__title {
  color: #131313;
}

.ice-cream__title:before {
  content: url("ice-cream_480 1x.68e6a8e3.jpg");
  width: 200px;
  height: 200px;
  border-radius: 100px;
}

.about {
  background-color: #84a178;
}

.about__header-container {
  width: 210px;
  margin: auto;
  padding-top: 60px;
}

.about__label {
  color: #fcfcfc;
  text-align: center;
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: 700;
}

.about__tittle {
  color: #fcfcfc;
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  font-family: Seymour One, sans-serif;
  font-size: 30px;
  font-weight: 900;
}

.about__img-container {
  width: 85%;
  height: 278px;
  border-radius: 50%;
  margin: 0 auto;
  overflow: hidden;
  box-shadow: 0 -22px 0 5px #f8d24eb3, 0 -22px 0 5px #d9d9d9;
}

.about__txt-and-btn-container {
  width: 220px;
  flex-direction: column;
  align-items: center;
  margin: auto;
  display: flex;
}

.about__texts {
  color: #fcfcfc;
  letter-spacing: .04em;
  text-align: center;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.76;
}

.about__read-more-btn {
  color: #000;
  width: 164px;
  height: 42px;
  cursor: pointer;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  margin-top: 24px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.about__read-more-btn--svg {
  width: 14px;
  height: 14px;
  position: relative;
  top: 4px;
  left: 4px;
}

.read-more-modal {
  width: 280px;
  height: 459px;
  cursor: auto;
  background: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  margin: auto;
  position: relative;
}

.read-more-modal__close-button {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: relative;
  top: 2%;
  left: 84%;
}

.read-more-modal__tittle {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: 0 auto 20px;
  font-family: Seymour One, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.read-more-modal__video-frame {
  width: 80%;
  margin: 0 auto;
}

.read-more-modal__video {
  width: 100%;
  border-radius: 10px;
}

.read-more-modal__text {
  width: 80%;
  margin: 20px auto 0;
  padding-bottom: 28px;
  font-family: Montserrat, sans-serif;
}

